import { Component, Input, OnInit } from '@angular/core';
import { AlertController, ModalController, PopoverController } from '@ionic/angular';
import { InicioService } from '../../../providers/Almacenamiento/inicio.service';
import { BiografiaService } from '../../../providers/Api/Biografia.service';
import { ApiSeguimientoService } from '../../../providers/Api/Seguimiento.service';
import { UsuariosLoginService } from '../../../providers/Api/UsuariosLogin.service';
import { ToastServices } from '../../../providers/Utilidades/ToastService.service';
import { Router } from '@angular/router';
import { TicketsService } from '../../../providers/Api/Tickets.service';
import { Socket } from 'ngx-socket-io';
import { THIS_EXPR } from '@angular/compiler/src/output/output_ast';
import { Observable } from 'rxjs';
import { PedidosService } from '../../../providers/Api/Pedidos.service';

@Component({
  selector: 'app-modal-mapas',
  templateUrl: './modal-mapas.page.html',
  styleUrls: ['./modal-mapas.page.scss'],
})
export class ModalMapasPage implements OnInit {

  @Input() info;
  @Input() vista;
  @Input() seleccionados;

  contenido = {};
  servicios = [];
  estado = true;
  ejecutado = false;
  amigos = false;

  negocios = [];
  escogidos = [];

  cargando = false;

  paises = [];
  ciudades = [];
  miciudad = '';
  pais = '';
  ciudad = false;

  solicitudEnviada = false;
  estadoSolicitud = false;
  titulo = 'Enviar solicitud';
  id = '';
  nombre = '';

  cargado = false;
  listaModulos = '';

  constructor(
    private modalCtrl: ModalController,
    private almacenamiento: InicioService,
    private biografia: BiografiaService,
    private seguimientoService: ApiSeguimientoService,
    private toasCtrl: ToastServices,
    private popov: PopoverController,
    private apiUsuario: UsuariosLoginService,
    private alert: AlertController,
    private router: Router,
    private apiTickets: TicketsService,
    private socket: Socket,
    private apiPedidos: PedidosService
  ) { 
    this.escucharMensajes().subscribe((data: any) => {
      // console.log(data, ' data suscribe')
      if (data.msg) {
        this.toasCtrl.toastExitoso('Ticket creado correctamente');
        this.router.navigate(['/central/tickets'])
        this.cerrar();
      } else {
        this.toasCtrl.toastError('No se pudo crear el ticket, inténtelo más tarde');
      }
    })
  }

  ngOnInit() {
  }

  ionViewWillEnter() {
    
    if (this.vista === 1) {
      const portada = this.info.creador.portada.path ?  this.info.creador.portada.path  : 'cl';
      const imagen = this.info.creador.imagen.length > 0 ? 'https://motocaliapp.com/' + this.info.creador.imagen[0].path  : 'pl';
      this.info.portada = portada;
      this.id = this.info.creador._id;
      this.nombre = this.info.creador.nombrecompleto;
      this.info.imagen = imagen;
      this.informacion(this.info.creador._id);
      this.cargarSolicitud(this.info.creador._id);
      this.disponibilidad();
      this.modulos();
      // console.log(this.info);
    } else if (this.vista === 2) {
      this.listarPaises();
      this.listarNegocios();
    }
  }

  
  async modulos() {
    const info: any = await this.almacenamiento.obtenerToken();

    if ( info ) {
        this.apiUsuario.modulosTALLER(info.token, {_id: this.id}).subscribe((data: any) => {
          if (data.exe) {
            console.log(data.response, 'respon')
            if (data.response.length > 0) {
              this.listaModulos = data.response[0].modulos;
            }
          } else {
            this.toasCtrl.toastError('No se pudo cargar los módulos, intentelo más tarde');
          }
        }, (err) => {
          this.toasCtrl.toastError('No se pudo cargar los módulos, intentelo más tarde');
        })
    }

  }

  async disponibilidad() {
    const info = await this.almacenamiento.obtenerToken();

    if ( info ) {

        this.apiTickets.disponibilidadnegocio(info.token, {id: this.id}).subscribe((data: any) => {
          // console.log(data, 'DATONNNNNN')
          if (data.exe) {
            if (data.response === 'no') {
              this.cargado = false;
            } else {
              this.cargado = true;
            }
          } else {
          }
        }, (err) => {
          this.toasCtrl.toastError('No se pudo consultar la disponibilidad, inténtelo más tarde');
        })
      
    }
  }

  
 listarNegocios() {
  this.apiUsuario.negocios().subscribe((data: any) => {
    if (data.exe) {
      data.response.forEach((item) => {
        item.checked = true;
        this.escogidos.push(item.nombre);
      })
      this.negocios = data.response;
    } else {
      this.negocios.push('Taller Mécanico');
    }
  }, (err) => {
    this.negocios.push('Taller Mécanico');
  })
}



  async informacion(id) {
    // console.log(id)
    const info = await this.almacenamiento.obtenerToken();

    if ( info ) {
      this.biografia.getBiografia(info.token, id).subscribe((data: any) => {
        // console.log(data.response);
        if (data.response.length > 0) {
          // console.log(data);
          this.contenido = data.response[0].contenido;
          this.servicios = data.response[0].servicios;
          this.estado = false;
        } else {
          this.estado = true;
        }
  
        // // // console.log(`Estado ${this.estado}`);
    }, (err) => {
       // this.estado = true;
    });
    }
      
  }
  


  cerrar() {
    this.modalCtrl.dismiss({})
  }

  
  async cargarSolicitud(idReceptor: string) {
    // // console.log(idReceptor);
    const info = await this.almacenamiento.obtenerToken();

    if ( info ) {
    this.seguimientoService.consultarSolicitud(info.token, idReceptor).subscribe((data: any) => {
      // // console.log(data);

      if (data.response.length > 0) {
      
        if (data.response[0].estado === 'pendiente') {
          // console.log('NO AMIGOS')
          this.amigos = false;
          this.solicitudEnviada = true;
          this.titulo = 'Cancelar solicitud';
        } else if (data.response[0].estado === 'aceptado') {
          // console.log('SI AMIGOS')
          this.amigos = true;
          this.titulo = 'Dejar de seguir';
          this.solicitudEnviada = true;
        }

      } else {
        this.solicitudEnviada = false;
        this.titulo = 'Enviar solicitud';
      }
      this.ejecutado = true;

    }, (err) => {
      this.ejecutado = true;
      this.toasCtrl.toastError('No se pudo traer toda la información, inténtelo más tarde');
    })

  }
  }

  
listarPaises() {
  this.apiUsuario.listarPaises().subscribe((data: any) => {
    if (data.exe) {
      this.paises = data.response;
    } else {
      this.paises.push('Colombia');
    }
  }, (err) => {
    this.paises.push('Colombia');
  })
}


listarCiudades( event, opt? ) {
  // console.log(event.detail)
  this.pais = event.detail.value.nombre;
  this.miciudad = '';
  this.cargando = true;
  this.apiUsuario.listarCiudades(event.detail.value._id).subscribe((data: any) => {
   if (data.exe) {
     this.ciudades = data.response;
     this.ciudad = true;
     if (opt) {
       
 
     }
   } else {
     this.toasCtrl.toastError('No se pudo traer las ciudades, intente registrarse más tarde');
   }
   this.cargando = false;
 }, (err) => {
   this.toasCtrl.toastError('No se pudo traer las ciudades, intente registrarse más tarde');
   this.cargando = false;
 })
}

seleccionarCiudad( event ) {
 this.miciudad = event.detail.value;
}



  cambio( event ) {
    // console.log(event.detail);
    if (event.detail.checked) {
      if (!this.escogidos.find((item) => item === event.detail.value)) {
        this.escogidos.push(event.detail.value);
      }
    } else {
      this.escogidos = this.escogidos.filter((item) => item !== event.detail.value);
    }
    // console.log(this.escogidos);
  }

  guardar() {
    
    this.popov.dismiss({
      validado: true,
      arreglo: this.escogidos,
      ciudad: this.miciudad === '' ? false : this.miciudad,
      pais: this.pais === '' ? false : this.pais
    })
  }

  seguir() {

  }

 

  seguimiento() {
    if (!this.solicitudEnviada) {
     this.almacenamiento.obtenerToken().then((info: any) => {
        this.estadoSolicitud = true;
        this.seguimientoService.enviarSolicitud({
          token: info.token,
          IDreceptor: this.id
        }).subscribe((data: any) => {
          this.estadoSolicitud = false;
          this.solicitudEnviada = true;
          this.titulo = 'Cancelar Solicitud';
          // console.log(`Data del seguimiento ${data}`);
        }, (error) => {
           // console.log(error.message);
           this.estadoSolicitud = false;
           this.toasCtrl.alertApplication(error.message, false);
        });
        
    })
    } else {
      this.almacenamiento.obtenerToken().then((info: any) => {
      this.seguimientoService.cancelarSolicitud(info.token, this.id, '1').subscribe((data: any) => {
        this.solicitudEnviada = false;
        this.titulo = 'Seguir Cuenta';
      }, (error) => {
        this.toasCtrl.alertApplication(error.message, false);
      })

    }).catch((err) => {
  });
    }

}

async crearTicket() {
  const alert = await this.alert.create({
    header: 'Crear Ticket',
    // tslint:disable-next-line:max-line-length
    message: 'Al crear el ticket entrarás en la fila de espera de ' + this.nombre,
    inputs: [
      {
        type: 'textarea',
        placeholder: 'Asunto del ficho',
        name: 'des'
      }
    ],
    buttons: [
      {
        text: 'Cancelar',
        cssClass: 'cancelarAlerta',
        role: 'cancel',
        handler: () => {
          
        }
      },
      {
        text: 'Crear Ticket',
        cssClass: 'botonalerta',
        handler: (dt) => {
          this.generarTicket(dt);
        }
      }
    ]
  })

  await alert.present();
}



async crearPedido() {
  const alert = await this.alert.create({
    header: 'Crear Pedido',
    // tslint:disable-next-line:max-line-length
    message: 'Al crear el pedido se creará una sala de chat con ' + this.nombre,
    buttons: [
      {
        text: 'Cancelar',
        cssClass: 'cancelarAlerta',
        role: 'cancel',
        handler: () => {
          
        }
      },
      {
        text: 'Crear Pedido',
        cssClass: 'botonalerta',
        handler: async (dt) => {
           if (this.id !== '') {
            const info = await this.almacenamiento.obtenerToken();
              
            if ( info ) {
              this.apiPedidos.crearPedido(info.token, {taller: this.id}).subscribe((data: any) => {
                console.log(data);
                if (data.exe) {
                  this.modalCtrl.dismiss();
                  this.toasCtrl.toastExitoso('Pedido creado correctamente');
                  this.router.navigate(['/central/pedidos'])
                } else {
                  this.toasCtrl.toastError('No se pudo generar el pedido para ' + this.nombre);
                }
              }, (err) => {
                this.toasCtrl.toastError('No se pudo generar el pedido para ' + this.nombre);
              })
            }
          } else {
            this.toasCtrl.toastError('No se pudo generar el pedido para ' + this.nombre);
          }
        }
      }
    ]
  })

  await alert.present();
}

async generarTicket(dt) {
  if (this.id !== '') {
    const info = await this.almacenamiento.obtenerToken();

    if ( info ) {
      this.socket.emit('crear', {idtaller: this.id, id: info._id, usuario: info.nusuario, nombre: info.nombre, des: dt.des} );
    }
  } else {
    this.toasCtrl.toastError('No se pudo generar el ticket para ' + this.nombre);
  }
  
}

escucharMensajes() {
  const myobserver = new Observable(observer => {
    this.socket.on('mensajeTicket', (data) => {
      observer.next(data);
    });
  });

  return myobserver;
}

}
